<template>
<div>
    <b-jumbotron fluid>
        <b-container>
            <b-alert class="mt-2 bigz" :show="paymentStatus == 'ok'" variant="success">
                <h1>Оплата подтверждена и получена! Через пару минут прийдет письмо на почту с доступом к курсу </h1>
            </b-alert>
            <b-alert class="mt-2" :show="paymentStatus == 'no confirmation'" variant="info">Процесс запушен, но подтверждения оплаты пока нет. Напишите на почту kotov@bpmn2.ru, если в течение 30 минут не получите доступ к курсу.</b-alert>
            <h1>Оплатите участие в курсе</h1>
            <p>Лучшие знания по BPMN, доступные онлайн</p>
            <b-card>

                <b-form>
                    <b-form-group id="input-group-1" label="Email*" label-for="input-1" description="На него будет выслано приглашение от системы обучения">

                        <b-form-input id="input-1" v-model="email" v-autofocus :state="nameState" type="email" required placeholder="Введите email"></b-form-input>
                        <b-form-invalid-feedback id="input-1-feedback">
                            Введите email вида х@x.x
                        </b-form-invalid-feedback>

                    </b-form-group>
                    <b-button size="sm" @click="showPromoCode" variant="link">У меня есть промокод</b-button>
                    <b-form-group v-show="promoCodeShow" id="input-group-2">
                        <b-form inline>
                            <b-form-input v-on:blur="updatePrice" id="input-2" v-model="promoCode" :state="discounted ? true : null" placeholder="Введите промокод"></b-form-input>
                            <b-button class="ml-2" @click="getPrice" variant="outline-warning">Применить</b-button>
                        </b-form>

                    </b-form-group>

                    <b-card-group deck>
                        <b-card title="0704: Быстрое погружение в BPMN ">
                            <b-card-text>Лучший способ быстро разобраться в BPMN от нуля до самостоятельного создания качественных схем.</b-card-text>

                            <div slot="footer">
                                <h5>
                                    <b-form-radio v-model="courseCode" value="0704">Купить этот курс</b-form-radio>
                                </h5>
                            </div>
                        </b-card>

                        <b-card title="1204: Пилотное внедрение BPMS">
                            <b-card-text>Выжимка из опыта 30 проектов по внедрению BPMS от продажи идеи руководству до внедрения и оценки результатов.</b-card-text>
                            <div slot="footer">
                                <h5>
                                    <b-form-radio v-model="courseCode" value="1204">Купить этот курс</b-form-radio>
                                </h5>
                            </div>
                        </b-card>

                        <b-card title="2307: Camunda для оркестрации микросервисов">
                            <b-card-text>Курс для тех, кто хочет ускорить разработку микросервисов и облегчить их эксплуатацию, сделать микросервисы «видимыми» и дать понимание происходящего новым разработчикам </b-card-text>
                            <div slot="footer">
                                <h5>
                                    <b-form-radio v-model="courseCode" value="2307">Купить этот курс</b-form-radio>
                                </h5>
                            </div>
                        </b-card>
                    </b-card-group>
                    <h2 class="mt-3 mb-3 text-center">

                        <span class="discounted" v-if="discounted">{{priceRespose.basePrice}}</span>
                        {{ priceRespose.discountPrice >0 ? priceRespose.discountPrice : priceRespose.basePrice}} рублей
                    </h2>
                    <b-button @click="clickModal" block size="lg" class="mt-2" variant="primary" :disabled="clicked || !nameState">
                        Начать обучение
                    </b-button>
                </b-form>
            </b-card>

            <b-modal hide-footer v-model="modalBool" title="Оплата">
                <p class="text-center"> 7 дней возврат средств, если вам что-то не понравится. </p>
                <div id='arsenalpay-widget'></div>
            </b-modal>
             <a href="mailto:kotov@bpmn2.ru?subject=Запрос счёта&body=Прошу выставить счет на курс ....">Оплата для юр.лиц</a> 

        </b-container>
    </b-jumbotron>
</div>
</template>

<script>
var API_ENDPOINT = '/api/v1/';
if (process.env.NODE_ENV != 'production') {
    API_ENDPOINT = "http://localhost:8080/api/v1/"
}
export default {
    name: "Payment",
    data() {
        return {
            courseCode: "0704",
            promoCode: null,
            clicked: false,
            email: null,
            paymentStatus: null,
            promoCodeShow: false,
            basePrice: 8900,
            discountPrice: 0,
            discounted: false,
            priceRespose: null,
            modalBool: false,
            arsenal: null,
        }
    },

    watch: {
        courseCode: function (val) {
            this.getPrice()
        }
    },
    computed: {
        nameState() {
            var re = /\S+@\S+\.\S+/;
            if (this.email == null) {
                return null
            } else return re.test(this.email);
        },
        baseUrl() {

        }
    },
    methods: {
        getPrice() {
            this.axios.get(API_ENDPOINT + "/checkPrice?courseCode=" + this.courseCode + "&promoCode=" + this.promoCode).then(response => {
                this.priceRespose = response.data;
                if (this.priceRespose.discountPrice == 0) {
                    this.discounted = false
                }
                if (this.priceRespose.discountPrice != 0) {
                    this.discounted = true
                }
            })
        },
        validateEmail(email) {
            var re = /\S+@\S+\.\S+/;
            return re.test(email);
        },
        showPromoCode() {
            this.promoCodeShow = !this.promoCodeShow
        },
        updatePrice() {
            this.getPrice();
        },
        clickModal() {
            this.getPrice();
            this.arsenal.destination = this.email + this.courseCode + this.promoCode;
            if (this.discounted == true) {
                this.arsenal.amount = this.priceRespose.discountPrice;
            } else {
                this.arsenal.amount = this.priceRespose.basePrice;
            }
            this.modalBool = true;

            setTimeout(() => {
                this.axios.get(API_ENDPOINT + "/callback/arsenalpay/prepareArsenalPay/" + this.email + "?courseCode=" + this.courseCode + "&&promoCode=" + this.promoCode).then(response => {
                    this.arsenal.element = 'arsenalpay-widget';
                    this.arsenal.render();
                })
            }, 100);

        },
        generateUrl() {
            this.clicked = true;
            this.axios.get(API_ENDPOINT + "/generateUrl/" + this.email + "?courseCode=" + this.courseCode + "&promoCode=" + this.promoCode).then(response => {
                window.location.href = response.data;
            })
        },
        checkStatus() {

            this.axios.get(API_ENDPOINT + "/checkPayment/" + this.email).then(response => {
                this.paymentStatus = response.data;
            })

        }
    },
    mounted() {
        if (this.$route.query.courseCode) {
            this.courseCode = this.$route.query.courseCode;
        }
        this.getPrice()

        if (this.$route.query.email) {
            this.checkStatus()
        }

        setInterval(() => {
            this.checkStatus()
        }, 3000);

        let recaptchaScript = document.createElement('script')
        recaptchaScript.setAttribute('src', 'https://arsenalpay.ru/widget/script.js')
        document.head.appendChild(recaptchaScript)

        setTimeout(() => {
            this.arsenal = new ArsenalpayWidget();
            this.arsenal.element = 'arsenalpay-widget';
            this.arsenal.widget = 2227;
            console.log(this.email);
            this.arsenal.destination = this.email + this.courseCode + this.promoCode;
        }, 300);

    },
    created() {

    },
}
</script>

<style>
.discounted {
    text-decoration: line-through;
    color: #2a2a2aa3;
}

.jumbotron {
    height: 100vh;
}

.bigz {
    z-index: 99999
}
</style>
